  z-index: 2;
}
.advantageCard::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: -5%;
  width: calc(100% - 16px);
  height: 100%;
  border-radius: 16px;
  border: 1px solid #C0C2C7;
  z-index: 0;
}
.advantageCard::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 16px;
  background: #F4F7FD;
  filter: drop-shadow(0px 12px 27.1px rgba(12, 30, 62, 0.12));
